



















































































































































































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
import Footer from '@/components/footer.vue'
export default {
	mixins:[mixin],
	components:{
		'v-footer':Footer
	},
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			vipawardlist:[],
			is_show:true,
			bid:'',
			business:{},
			currect_grade:{},
			currect_speed:'',
			left_grade:'',
			currect_total:0,
			nosign:0,
			allowLoad:true,
			error_show:false,
			isSeller:false
		};
	},
	methods:{
		async init(){
			
			Utils.loading('正在加载')
			let response = await api.initIdentify({uid:this.userInfo.uid})
			
			console.log(response)
			if(response.status == 1){
				Utils.loaded()
				let data = response.data;
				this.$store.commit('set_userInfo', data);
				this.userInfo = data
				// 商家端判断
				if(this.userInfo.identity == 3){
					this.allowLoad = true
					this.bid = this.userInfo.bid
				}
				// 店员端判断
				if(this.userInfo.seller == this.bid){
					console.log('laile')
					this.isSeller = true
				}
			}else{
				Utils.loaded()
				Utils.toast(response.message)
			}
			
			
			if(this.userInfo.identity == 1 && !this.isSeller){
				if(!this.is_show){
					uni.showTabBar({
						
					})
					
				}else{
					uni.hideTabBar({
						
					})
				}
			}else{
				this.is_show = false
				uni.hideTabBar({
					
				})
			}
			if(this.allowLoad){
				// 获取首页的信息
				let data = {
					uid:this.userInfo.uid,
					bid:this.bid
				}
				let indexInfo = await api.getIndex(data)
				
				if(indexInfo.status == 1){
					this.business = indexInfo.data.business
					this.currect_grade = indexInfo.data.currect_grade
					this.vipawardlist[0] = {
						id:this.currect_grade.id,
						title:this.currect_grade.grade_title,
						desc:this.currect_grade.description,
						photo:this.currect_grade.grade_photo,
						bg:this.currect_grade.grade_bg,
						current:true
					}
					this.currect_speed = indexInfo.data.currect_speed
					this.left_grade = indexInfo.data.left_grade
					this.currect_total = indexInfo.data.currect_total || 0
					this.nosign = this.currect_grade.sign_time - this.currect_total
				}else{
					Utils.toast(response.message)
				}
			}else{
				this.is_show = false
				this.error_show = true
			}
			
		},
		navigateto(type,params){
			if(params){
				uni.navigateTo({
					url:type+'?id='+params
				})
			}else{
				uni.navigateTo({
					url:type
				})
			}
		},
		changeVip(e) {
			let vipid = e.detail.current
			this.currenid = vipid
		},
		showModel(){
			let isshow = this.is_show
			if(isshow){
				this.is_show = false
				uni.showTabBar({
					
				})
			}else{
				this.is_show = true
			}
		},
		scanVerify(){
			uni.scanCode({
				onlyFromCamera: true,
				success:function(res){
					console.log(res)
				}
			})
		}
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	},
	onLoad(options){
		// 拿到扫码的bid值
		let storagebid = uni.getStorageSync("bid")
		if(storagebid && !options.scene){
			this.bid = storagebid
		}else{
			if(options.scene){
				let sceneData = options.scene.split('_')
				let type = sceneData[1]
				if(type == 'user'){
					let bid = sceneData[0]
					if(bid){
						this.bid = bid
						uni.setStorageSync("bid",bid)
					}else{
						this.allowLoad = false
					}
				}else{
					let dataid = sceneData[0]
					uni.navigateTo({
						url:'/pages/scan/scan?id='+dataid+'&type='+type
					})
				}
			}else{
				this.allowLoad = false
			}
		}
		
	}
}
